:root {
    --column-width: 640px;
    --column-width-double: calc(var(--column-width) * 2);
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: calc(var(--column-width) / 20);
    background-image: url(background.webp);
    background-size: cover;
    background-position: right center;
}

a[href],
a[href]:visited {
    color: #321;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.App * {
    display: inline-block;
}

.App section h1 {
    margin: .5em 0;
}

.App section {
    display: block;
    margin: 0;
    padding: .5em;
    max-width: 100vw;
    overflow-x: hidden;
    overflow-y: auto;
    text-align: center;
}

.App section>* {
    text-align: left;
    padding: 0;
    margin: 0;
    width: var(--column-width);
    vertical-align: top;
}

.App section>div.fig {
    display: block;
    width: 100vw;
    text-align: center;
    padding: 0;
    margin: 0;
}

.App section>div.fig>* {
    text-align: left;
    width: var(--column-width);
    padding: 0;
    margin: 0;
    vertical-align: top;
}

.App section>div.fig>figure {
    margin-left: -1rem;
}

@media (max-width: 640px) {
    :root {
        --column-width: 100vw;
    }
}